<template>
    <div class="content-table">
        <div class="center">
            <vs-table>
                <template #thead>
                <vs-tr>
                    <vs-th>Icon</vs-th>
                    <vs-th>Name Application</vs-th>
                    <vs-th>Dev</vs-th>
                    <vs-th>Count</vs-th>
                    <vs-th>Action</vs-th>  
                </vs-tr>
                </template>
                <template #tbody>
                <vs-tr
                    :key="i"
                    v-for="(tr, i) in $vs.getPage(applicationsSettingLimits, page, max)"
                    :data="tr" >
                    <vs-td>
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="tr.url"
                        fit="cover"></el-image>
                    </vs-td>
                    <vs-td>
                    {{ tr.name_applications }}
                    </vs-td>
                    <vs-td>
                    {{ tr.name }}
                    </vs-td>
                    <vs-td>
                    {{tr.limit_applications}}
                    </vs-td>
                    <vs-td>
                        <el-dropdown>
                        <span class="el-dropdown-link">
                            Action<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="detail(tr)" icon="el-icon-check">Detail</el-dropdown-item>
                            <el-dropdown-item @click.native="open(tr)" icon="el-icon-edit">Edit</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </vs-td>
                </vs-tr>
                </template>
                <template #footer>
                <vs-pagination v-model="page" :length="$vs.getLength(applicationsSettingLimits, max)" />
                </template>
            </vs-table>
            <el-dialog title="Detail App" :visible.sync="dialogTableVisible">
              <el-form class="form-application-add" label-position="left" label-width="250px">
                <el-form-item label="Username Develop">
                {{ dataPopUp.name }}
                </el-form-item>
                <el-form-item label="Email">
                {{ dataPopUp.email }}
                </el-form-item>
                <el-form-item label="Icons">
                  <el-image
                    style="width: 100px; height: 100px"
                    :src="dataPopUp.url"
                    fit="cover">
                  </el-image>
                </el-form-item>
                <el-form-item label="Name App">
                {{ dataPopUp.name_applications }}
                </el-form-item>
                <el-form-item label="Package App">
                {{ dataPopUp.package_applications }}
                </el-form-item>
                <el-form-item label="Limit">
                {{ dataPopUp.limit_applications }}
                </el-form-item>
              </el-form>
            </el-dialog>
        </div>
    </div>
</template>

<script>

import axios from '../../../axios.js'

export default {
    data() {
      return {
        dialogTableVisible : false,
        page: 1,
        max: 5,
        applicationsSettingLimits : [],
        dataPopUp : {}
      }
    },
    methods : {
      detail (data) {
        this.dataPopUp = data
        this.dialogTableVisible = true
      },
      open (data) {
        const dataInput = data
        this.$prompt('Please input your e-mail', 'Tip', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          inputErrorMessage: 'Invalid Count'
        }).then(({ value }) => {
          const obj = {
            id : dataInput.id_applications,
            id_user : this.$store.state.userDataActive.id,
            count : value,
          }
          const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
          }
          axios.post('application/setting/limit/edit', obj, accessToken)
            .then((response) => {
                if (response.data.success == true) {     
                  const item = {
                    id_applications : dataInput.id_applications,
                    id_user : dataInput.id_user,
                    id_icons : dataInput.id_icon,
                    name_applications : dataInput.name_applications,
                    package_applications : dataInput.package_applications,
                    key_applications : dataInput.key_applications,
                    limit_applications : parseInt(value),
                    created_at : dataInput.created_at,
                    name : dataInput.name,
                    email : dataInput.email,
                    role : dataInput.role,
                    folder : dataInput.folder,
                    extension : dataInput.extension,
                    url : dataInput.url,
                  } 
                  const Index = this.applicationsSettingLimits.findIndex((p) => parseInt(p.id_applications) === parseInt(dataInput.id_applications))
                  Object.assign(this.applicationsSettingLimits[Index], item)
                  this.$message({
                    type: 'success',
                    message: 'Your Update ' + response.data.data
                  });
                } else {
                  this.$message({
                    type: 'Danger',
                    message: 'Update Failed'
                  });       
                }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Input canceled'
          });       
        });
      }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        }
    },
    created () {
      const accessToken = {
        headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
      }
      axios.get('application/setting/limit/show', accessToken)
        .then((response) => {
            if (response.data.success == true) {
              this.applicationsSettingLimits = response.data.data
            }
        })
    }
 
}
</script>

<style>