<template>
<el-row :gutter="24">
  <el-col :span="8" v-loading="loading">
        <el-form  label-position="left" label-width="100px" :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item label="Username" prop="username">
                <el-input type="Text" v-model="ruleForm.username" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Email" prop="email">
                <el-input type="Text" v-model="ruleForm.email" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Password" prop="password">
                <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" size="mini" @click="submitForm('ruleForm')">Add User</el-button>
                <el-button size="mini" @click="resetForm('ruleForm')">Reset</el-button>
            </el-form-item>
        </el-form>
  </el-col>
  <el-col :span="16">
    <vs-table>
      <template #thead>
        <vs-tr>
          <vs-th>Icon</vs-th>
          <vs-th>Username</vs-th>
          <vs-th>Email</vs-th>
          <vs-th>Role</vs-th>
          <vs-th>Action</vs-th>  
        </vs-tr>
        </template>
        <template #tbody>
          <vs-tr
              :key="i"
              v-for="(tr, i) in $vs.getPage(itemUsers, page, max)"
              :data="tr" >
            <vs-td>
            <el-image v-if="(!tr.url)"
              style="width: 100px; height: 100px"
              :src="tr.photo"
              fit="cover"></el-image>
            <el-image v-else
              style="width: 100px; height: 100px"
              :src="tr.url"
              fit="cover"></el-image>
            </vs-td>
            <vs-td>
              {{ tr.username }}
            </vs-td>
            <vs-td>
              {{ tr.email }}
            </vs-td>
            <vs-td>
              {{ tr.role }}
            </vs-td>
            <vs-td>
              <el-dropdown>
                <span class="el-dropdown-link">
                  Action<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
              <el-dropdown-menu slot="dropdown">
              <el-dropdown-item icon="el-icon-check">Detail</el-dropdown-item>
              <el-dropdown-item icon="el-icon-edit">Edit</el-dropdown-item>
              <el-dropdown-item icon="el-icon-delete">Delete</el-dropdown-item>
              </el-dropdown-menu>
              </el-dropdown>
            </vs-td>
            </vs-tr>
            </template>
            <template #footer>
              <vs-pagination v-model="page" :length="$vs.getLength(itemUsers, max)" />
            </template>
            </vs-table>
  </el-col>
</el-row>
</template>

<script>

import moduleUsers from '../../../store/users/moduleUsers.js'

export default {
    data () {
      var validateUsername = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input the Username'));
        } else {
          callback();
        }
      };
      var validateEmail = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input the Email'));
        } else {
          callback();
        }
      };
      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input the password again'));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('Two inputs don\'t match!'));
        } else {
          callback();
        }
      };
      return {
        loading : false,
        page: 1,
        max: 3,
        ruleForm: {
          username : '',
          email : '',
          password : '',
        },
        rules: {
          username: [
            { validator: validateUsername, trigger: 'blur' }
          ],
          email: [
            { validator: validateEmail, trigger: 'blur' }
          ],
          password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
        }
      };
    },
    computed : {
      itemUsers () {
        return this.$store.state.dataUsers.userMembers
      },
    },
    methods: {
      submitForm(formName) {
        this.loading = true
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$store.dispatch('dataUsers/addUsers', this.ruleForm)
            .then(response => {
              if (response.data.success === true) {
                this.$notify.success({
                  title: 'Success',
                  message: 'This is a success message',
                  offset: 100
                })
                this.loading = false        
              this.$refs[formName].resetFields();
              } else {
                this.$notify.error({
                  title: 'Error',
                  message: 'This is a Error message',
                  offset: 100
                });
              }
            })
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Invalid Data Input',
              offset: 100
            });
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    created () {
      if (!moduleUsers.isRegistered) {
        this.$store.registerModule('dataUsers', moduleUsers)
        moduleUsers.isRegistered = true
      }
    this.$store.dispatch('dataUsers/showUsers')
    }
  }
</script>

<style>
.add-users {
    width: 600px;
    margin: 0 auto;
    margin-top: 50px;
}
</style>