
import axios from '../../axios'
const accessToken = {
  headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
}


export default {
  addUsers ({ commit }, item) {
    return new Promise((resolve, reject) => {
      axios.post('user/insert', { item }, accessToken)
        .then((response) => {
          commit('ADD_USERS', Object.assign(response.data.data))
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  showUsers ({ commit }) {
    return new Promise((resolve, reject) => {
      axios.get('user/show', accessToken)
        .then((response) => {
          commit('SET_USERS', response.data.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  }
}