<template>
    <el-row class="line-bottom">
        <el-col class="line-right" :span="5">
            <el-image
                style="width: 150px; height: 150px"
                :src="url"
                fit="cover">
            </el-image>
        </el-col>
        <el-col v-if="editProfile" :span="8">
            <el-form class="form-application-add" label-position="left" label-width="150px">
                <el-form-item label="Username">
                {{ username }}
                </el-form-item>
                <el-form-item label="Email">
                {{ email }}
                </el-form-item>
                <el-form-item label="Password">
                    ********
                </el-form-item>
                <el-form-item label="Role">
                {{ role }}
                </el-form-item>
                <el-form-item>
                    <el-button type="warning" icon="el-icon-edit" @click="editDataProfile()" size="mini">Edit Profile</el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <el-col v-else :span="15">
            <el-form class="form-application-add" label-position="left" label-width="150px" :model="ruleForm" :rules="rules" ref="ruleForm">
                <el-form-item label="Username" prop="username">
                    <el-input type="Text" v-model="ruleForm.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="Email">
                    {{email}}
                </el-form-item>
                <el-form-item label="Icon">
                    <el-upload
                        class="upload-demo"
                        action=""
                        :file-list="fileList"
                        :auto-upload="false"
                        :limit="1"
                        list-type="picture">
                        <el-button size="small" type="primary">Click to upload</el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 500kb</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="Password">
                    <el-button type="danger" icon="el-icon-edit" @click="editPassword = true" size="mini" v-if="!editPassword">Edit</el-button>
                    <el-button type="warning" icon="el-icon-close" @click="editPassword = false" size="mini" v-else>Cancel</el-button>
                </el-form-item>
                <el-form-item label="Old Password" v-if="editPassword">
                    <el-input type="password" v-model="passwordOld" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="New Password" v-if="editPassword">
                    <el-input type="password" v-model="passwordNew" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-edit" @click="editDataProfile('ruleForm')" size="mini">Submit</el-button>
                    <el-button type="warning" icon="el-icon-close" @click="editDataProfile()" size="mini">Cancel</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>

// import axios from '../../../axios.js'

export default {
    data () {
        return {
            editPassword : false,
            ruleForm : {
                username : '' },
            editProfile : true,
            passwordOld : '',
            passwordNew : '',
            fileList : [],
            rules: {
            },
            formData : new FormData(),
        }
    },
    methods : {
        editDataProfile () {
            
        }
    },
    created () {
        this.editProfile = true
        this.username = this.$store.state.userDataActive.user
        this.email = this.$store.state.userDataActive.email
        this.role = this.$store.state.userDataActive.role
        this.url = this.$store.state.userDataActive.photo
    }
}
</script>